.header{
    position: fixed;
    width: 100%;
    height: 128px;
    z-index: 10;
    mix-blend-mode: difference;

    /* @include media('<=phone'){
        //height: 96px;
    } */

    .logo{
        a{
            font-family: "Editorial New", "Helvetica Neue", sans-serif;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            color: $white;
            text-decoration: none;
            //font-size: 55rem;
            font-size: 5vh;
            letter-spacing: .05rem;
            //font-weight: 55;
            z-index: 10;
        }
    }
    .nav{
        a{
            font-family: "Editorial New", "Helvetica Neue", sans-serif;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            color: $white;
            font-size: 5vh;
            letter-spacing: .05rem; 
            text-decoration: none;
            //font-weight: 55;
            mix-blend-mode: difference;
            z-index: 10;

        }
         //width: 25px;
        /*  @include media('<=phone'){
             width: 100%;
         } */
         span{
             margin-bottom: .3rem;
             background: $black;
             height: 2px;
             width: 100%;
             display: block;
         }
            
    }
}