.main{
   //height: 80vh; //fallback option
   // height: calc(var(--vh,1vh)*80);
   width: 100%;
    background-color: $white;
    .row{
        display: block !important;
        //flex-direction: column;
        // justify-content: center;
        h2{
            margin-top: 0 !important;
            z-index: 5;
            font-size: 5vh;
            line-height: 63px;
            font-weight: 55;
            text-align: justify;
            color: $black;
            text-transform: uppercase;
           // margin-bottom: 18%;
             @include media("<=tablet"){
                
                line-height: 3rem;
                margin-bottom: 25%;
            }
            @include media ("<=phone"){
                margin-top: 72px;
                
                line-height: 2.2rem;
            } 
            
        }

        .mar{
            width: 100%;
            margin-bottom: 10%;
        }

        img{
            display: block;
            margin-left: auto;
            margin-right: auto;
        }

        .half{                     
            width: 50%;
            margin-left: auto; 
            margin-right: 0;
            margin-top: 5%;
            @include media ("<=phone"){
                width: 100%;
            } 
         }
        .sub{                     
            text-transform: uppercase;
            font-size: 3vh;
            font-weight: 400;
            color: $black;
         }
         
    }
}