.cases{
    width: 100%;
     //height:150vh;
     //height: calc(var(--vh,1vh)*150);
     background-color: $white;
    .row{
        display: block !important;
        .case{
        background-color: $white;
        //  position: relative;
        //  background: $black;
        //  cursor: pointer;
        margin-top: 0 !important;
        margin-bottom: 0 !important;
        padding-top: 5% !important;
        padding-bottom: 5% !important;
        
        .case-details{
            width: 100%;
            //height: 100%;
            flex-direction: column;
            justify-content: center;
            // height: calc(var(--vh,1vh)*50);
            //    box-sizing: border-box;
            //    z-index: 1;
            //   position: relative;

            .norow{
                display: flex !important;
                @include media('<=phone'){
                    display:block !important;
                    flex-direction: column !important;
             }
            }

            .der{
                display: flex;
                right: 0;
                @include media('<=phone'){
                    display:block !important;
             }
            }

            span{
                //   margin-top: 156px;
                font-size: 5vh;
                font-weight: 55;
                color: $black;
                text-transform: uppercase;
            }

                .sub{           
                    font-family: "Editorial New", "Helvetica Neue", sans-serif;
                     -webkit-font-smoothing: antialiased;
                     -moz-osx-font-smoothing: grayscale;
                     letter-spacing: 3%;          
                    text-transform: uppercase;
                    font-size: 3vh;
                    font-weight: 400;
                    color: $black;
                 }

                /* .des{
                    text-align: center !important;
                    margin:0 auto !important;
                    width: 50% !important;
                    font-size: 38px !important;
                    font-weight: 400 !important;
                    line-height: 43px !important;
                    color: $black;
                } */
                
                h2{
                    font-family: "Editorial New", "Helvetica Neue", sans-serif;
                     -webkit-font-smoothing: antialiased;
                     -moz-osx-font-smoothing: grayscale;
                    text-align: center;
                    font-size: 100px;
                    letter-spacing: -7% !important;
                    font-weight: 400;
                    text-transform: uppercase;
                    // width: 85%;
                    // margin-top: 16px;
                    color: $black;
                    //margin-bottom: 5% !important; 
                }
            }
            .case-image{
                top: 0;
                left: 0;
                transition: .4s cubic-bezier(.6,-.05,.1,.99);
                img{
                //  height: 50%;
                    width: 100%;
                    margin-top: 20px !important;
                    margin-bottom: 20px !important;
                //  object-fit: cover;
                }

                .half-left{
                    width: 50%;
                    right: 0;
                    @include media('<=phone'){
                           width: 100%;
                    }
                }
            }

        }

    }
}